@media screen and (min-width: 1125px) {
  .main-image-bg {
    background-image: url("../images/main.png");
    background-position: 50% 50%;
    background-size: cover;
    min-height: 650px;
    background-repeat: no-repeat;
  }

  .main-learn-more {
    padding-top: 10%;
    text-align: left;
    padding-left: 5%;
  }

  .main-learn-more-font-size {
    font-size: 28px;
  }

  .contact-media-format {
    margin: 0 12% 0 12%;
  }
}

@media screen and (max-width: 1125px) and (min-width: 725px) {
  .main-image-bg {
    background-image: url("../images/main.png");
    background-position: 50% 50%;
    background-size: cover;
    min-height: 650px;
    background-repeat: no-repeat;
  }
  .main-learn-more {
    padding-top: 17%;
    text-align: left;
    padding-left: 5%;
  }
  .main-learn-more-font-size {
    font-size: 24px;
  }
  .contact-media-format {
    margin: 0 7% 0 7%;
  }
}

@media screen and (max-width: 725px) {
  .main-image-bg {
    background-image: url("../images/main.png");
    background-position: 0% 0%;
    background-size: contain;
    min-height: 500px;
    background-repeat: no-repeat;
  }
  .main-learn-more {
    padding-top: 75%;
    text-align: left;
    padding-left: 5%;
  }
  .main-learn-more-font-size {
    font-size: 20px;
  }
  .contact-media-format {
    margin: 0 4% 0 4%;
  }
}
